import React, { Component } from 'react';
import AngularLogo from'../icons/angular.svg';
import VueLogo from '../icons/vuejs.svg';
import SassLogo from '../icons/sass.svg';
import LessLogo from '../icons/less.svg';
import NodejsLogo from '../icons/node-js.svg';
import ReactLogo from '../icons/react.svg';
import JavaLogo from '../icons/java.svg';
import BootstrapLogo from '../icons/bootstrap.svg';
import MongodbLogo from '../icons/mongodb.svg';
import MysqlLogo from '../icons/mysql.svg';
import Image from "./images";

class App extends Component {
  getContent() {
    switch(this.props.page) {
      case 1 :
        return (
          <React.Fragment>
            <div className = "app__container--right__content__section-container flex-box">
                <div className = "app__container--right__content__section-container--profile-image">
                  {/**/}
                  <Image />
                </div>
                <div className = "app__container--right__content__section-container--header-text">
                  Austin Nguyen
                  <div className = "app__container--right__content__section-container--text">
                    <span style = {{'marginBottom':'2.5vmin'}}>Age: 17</span>
                    <span>
                      Hello, my name is Austin Nguyen. I am a recent high school graduate who enjoys programming. As a hobbyist, I've created this website to showcase my progress
                    </span>
                </div>
              </div>
            </div>
            <div className = "app__container--right__content__section-container bottom">
              Tools I've Used
              <div className = "app__container--right__content__section-container--tools">
                <div className = "app__container--right__content__section-container--tooltip">
                  <img src={AngularLogo} className = "icon" alt="AngularJs"/>
                  <span className="text">AngularJs</span>
                </div>
                <div className = "app__container--right__content__section-container--tooltip">
                  <img src={VueLogo} className = "icon" alt="VueJs"/>
                  <span className="text">VueJs</span>
                </div>
                <div className = "app__container--right__content__section-container--tooltip">
                  <img src={SassLogo} className = "icon" alt="Sass"/>
                  <span className="text">Sass</span>
                </div>
                <div className = "app__container--right__content__section-container--tooltip">
                  <img src={LessLogo} className = "icon" alt="Less"/>
                  <span className="text">Less</span>
                </div>  
                <div className = "app__container--right__content__section-container--tooltip">
                  <img src={NodejsLogo} className = "icon" alt="NodeJs"/>
                  <span className="text">NodeJs</span>
                </div>
                <div className = "app__container--right__content__section-container--tooltip">
                  <img src={ReactLogo} className = "icon" alt="ReactJs"/>
                  <span className="text">ReactJs</span>
                </div>
                <div className = "app__container--right__content__section-container--tooltip">
                  <img src={JavaLogo} className = "icon" alt="Java"/>
                  <span className="text">Java</span>
                </div>
                <div className = "app__container--right__content__section-container--tooltip">
                  <img src={BootstrapLogo} className = "icon" alt="Bootstrap"/>
                  <span className="text">Bootstrap</span>
                </div>
                <div className = "app__container--right__content__section-container--tooltip">
                  <img src={MongodbLogo} className = "icon" alt="MongoDb"/>
                  <span className="text">MongoDb</span>
                </div>
                <div className = "app__container--right__content__section-container--tooltip">
                  <img src={MysqlLogo} className = "icon" alt="Mysql"/>
                  <span className="text">Mysql</span>
                </div>
              </div>
            </div>
          </React.Fragment>
        );
    case 2: 
      return (
        <React.Fragment>
          <div className = "app__container--right__content__section-container">
            Current Objective
            <div className = "app__container--right__content__section-container--text">
              As a student who is nearing college, I need to begin saving to pay off my future student debts. I figured a good way to 
            </div>
          </div>
        </React.Fragment>
      );
    default :
      return ('');
    }
  }
  render() {
    return (
      <div className="app__container--right__content">
        {this.getContent()}
      </div>  
    );
  }
}

export default App;